@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap");

body {
  color: #fff;
  font-family: Rubik, Inter, sans-serif;
}

a {
  text-decoration: none;
  color: #fff !important;
}

.MuiButton-root {
  color: #fff !important;
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  background-color: #161c24;
  opacity: 1;
}

.container-page {
  z-index: 1;
  max-width: 450px;
}

#w3a-modal {
  z-index: 2;
}

.join-container {
  background: #374151;
  border-radius: 32px;
  padding: 30px;
  min-width: 350px;
  min-height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.chain-container {
  height: 35px;
  width: 35px;
  margin-bottom: 3px;
  transform: translatey(0px);
  animation: float 4s ease-in-out infinite;
}

@keyframes float {
  0% {
    box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
    transform: translatey(0px);
  }
  50% {
    box-shadow: 0 25px 15px 0px rgba(0, 0, 0, 0.2);
    transform: translatey(-8px);
  }
  100% {
    box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
    transform: translatey(0px);
  }
}

.dark-dialog .MuiBackdrop-root {
  background-color: rgba(0, 0, 0, 0.9) !important;
}

.dark-dialog .MuiPaper-root {
  background: #374151;
  border-radius: 32px;
  padding: 30px;
  padding-top: 15px;
}

.dark-dialog-slim .MuiBackdrop-root {
  background-color: rgba(0, 0, 0, 0.9) !important;
}

.dark-dialog-slim .MuiPaper-root {
  background: #374151;
  border-radius: 32px;
}

.MuiNativeSelect-select {
  overflow: auto;
}
